onst props = defineProps<IStep>();\n\nasync function changeShowLog(){\n  if(props.status == \"waiting\")\n    return\n\n  if(!showLog.value){\n    let resp :IlistExportJobStepLogsResponse = await qcloudApi.listExportJobStepLogs(props.ID);\n    logs.value = resp?.logs ? [] : resp.logs;\n  }\n\n  showLog.value = !showLog.value\n  let rowInformationContainer = document.getElementById(props.ID)\n  if(rowInformationContainer != null)\n    rowInformationContainer.style.height = showLog.value \n    ? (Number.parseInt(rowInformationContainer.style.height.replace(\"px\",\"\")) +  88) + 'px'\n    : (Number.parseInt(rowInformationContainer.style.height.replace(\"px\",\"\")) -  88) + 'px'\n}\n\n\n</script>\n\n<template>\n  <div class=\"step-row\" :style=\"{height: (showLog ? `126px` : '40px')}\">\n    <span class=\"step-icon\" @click=\"changeShowLog()\">\n      <i id=\"play-icon\" class=\"mdi mdi-play\" v-if=\"!showLog\"></i>\n      <i id=\"play-icon\" class=\"mdi mdi-menu-down\" v-else></i>\n    </span>\n    <span>\n      <i class=\"mdi mdi-timer-sand step-status-icon\" v-if=\"props.status == 'waiting'\"></i>\n      <i class=\"mdi mdi-arrow-up-circle-outline step-status-icon\" v-else-if=\"props.status == 'running'\"></i>\n      <i class=\"mdi mdi-check-circle-outline step-status-icon\" v-else-if=\"props.status == 'success'\"></i>\n      <i class=\"mdi mdi-close-circle-outline step-status-icon\" v-else-if=\"props.status == 'failed'\"></i>\n    </span>\n    <span>{{props.name}}</span>\n    <div v-if=\"showLog\" class=\"log-container\" >\n      <h3>{{logs.map(e => e.toString())}}</h3>\n    </div>\n  </div>\n\n</template>\n<style scoped>\n.step-row{\n  display: grid;\n  grid-template-columns: 50px 20px 1200px;\n  width: 1302px;\n  height: 40px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  opacity: 1; \n}\n\n.step-icon{\n  padding-left: 20px;\n  width: 16px;\n  height: 16px;\n}\n\n#play-icon{\n  color: #E3E3E3;\n  padding-top: 12px;\n  padding-bottom: 12px;\n}\n.step-status-icon{\n  color: #149911;\n  padding-top: 12px;\n  padding-bottom: 12px;\n}\n\n.log-container {\n  display: block;\n  margin-left: 52px;\n  height: 88px;\n  width: 883px;\n}\n\n.mdi-timer-sand {\n  color: #A1A1A1;\n}\n\n.mdi-arrow-up-circle-outline {\n  color: #449DD1;\n}\n.mdi-check-circle-outline {\n  color: #149911;\n}\n.mdi-close-circle-outline {\n  color: #DF2935;\n}\n\n</style>\n"],"sourceRoot":""}]);
