CloudApi } from \"../../../../../domain/infrastructure/IQCloudApi\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { StatusType } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { IRouter } from \"../../../../../domain/presentation/IRouter\";\nimport { ILogout } from \"../../../../../domain/useCase/ILogout\";\n\ninterface Step {\n  ID: string;\n  name: string;\n  status: StatusType;\n}\ninterface PublishHistoryTableRow {\n  title: string;\n  createDate: string;\n  createdBy: string;\n  status: string;\n  platform: string;\n  steps: Array<Step>;\n  showInformation: boolean;\n  isMultiStepJob: boolean;\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst router = container.resolve<IRouter>(IRouter);\nconst qcloudApi = container.resolve<IQCloudApi>(IQCloudApi);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst emit = defineEmits([\"close\"]);\nconst showingTab = ref(<\"publish\" | \"download\">(\"publish\"));\nconst tableData = ref<Array<PublishHistoryTableRow>>();\n// const tableData = ref<Array<PublishHistoryTableRow>>([{\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut11',\n//   createDate: '11.11.2011, 16:06:35',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Success',\n//   steps: [\n//     { name: \"Set Up Job\", log: \"123123\", status: 'success' },\n//     { name: \"Create Project\", log: \"123123\", status: 'success' },\n//     { name: \"Get Models\", log: \"123123\", status: 'success' },\n//     { name: \"Pack Projects\", log: \"123123\", status: 'success' },\n//     { name: \"Upload Project\", log: \"123123\", status: 'success' }\n//   ],\n//   showInformation: false\n// }, {\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut12',\n//   createDate: '11.11.2011, 16:06:36',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Running',\n//   steps: [{ name: \"312\", log: \"123123\", status: 'success' }],\n//   showInformation: false\n// },\n// {\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut12',\n//   createDate: '11.11.2011, 16:06:36',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Success',\n//   steps: [{ name: \"312\", log: \"123123\", status: 'success' }],\n//   showInformation: false\n// },\n// {\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut12',\n//   createDate: '11.11.2011, 16:06:36',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Success',\n//   steps: [{ name: \"312\", log: \"123123\", status: 'success' }],\n//   showInformation: false\n// },\n// {\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut12',\n//   createDate: '11.11.2011, 16:06:36',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Fail',\n//   steps: [{ name: \"312\", log: \"123123\", status: 'success' }],\n//   showInformation: false\n// },\n// {\n//   jobID: 'cw2bdgssa-cnfa-9xxi-g11-ytyut12',\n//   createDate: '11.11.2011, 16:06:36',\n//   createdBy: 'admin@ahe.com.tr',\n//   platform: \"󰀷 󰀲 󰍹\",\n//   status: 'Success',\n//   steps: [{ name: \"312\", log: \"123123\", status: 'success' }],\n//   showInformation: false\n// }]);\nconst tableHeaders = [\n  { name: \"Title\", style: \"padding-left:20px\" },\n  { name: \"Create Date\", style: \"\" },\n  { name: \"Created By\", style: \"\" },\n  { name: \"Status\", style: \"\" },\n  { name: \"Platform\", style: \"\" },\n]\n\nwatch(() => showingTab.value, (_new, _old) => {\n  getApplicationJobHistory();\n})\n\nfunction logout() {\n  executor.execute(\n    async () => {\n      await executor.executeUseCase<ILogout>(ILogout);\n      router.push(\"/login\");\n    },\n    { loading: true }\n  );\n}\n\nconst getApplicationJobHistory = async () => {\n  let historyList = await qcloudApi.listApplicationJobHistory({ applicationID: viewModel.studio.appId, type: showingTab.value });\n\n  tableData.value = historyList.map(e => {\n    return {\n      title: e.ID,\n      createDate: e.createDate.toString(),\n      createdBy: e.createdBy,\n      status: e.status,\n      platform: !e.platform ? \"-\" : e.platform,\n      steps: [],\n      showInformation: false,\n      isMultiStepJob: e.isMultiStep\n    } as PublishHistoryTableRow\n  })\n}\n\ngetApplicationJobHistory();\n\nasync function openCloseRow(row: PublishHistoryTableRow) {\n  if (row.status == \"waiting\")\n    return\n\n  if (!row.showInformation) {\n    //@ApiCall\n    let jobSteps = await qcloudApi.listExportJobSteps(row.title, row.isMultiStepJob);\n    row.steps = jobSteps.map(e => {\n      return {\n        ID: e.ID,\n        name: e.name,\n        status: e.status\n      } as Step\n    });\n  }\n\n  row.showInformation = !row.showInformation;\n}\n\nasync function refreshTable() {\n  await getApplicationJobHistory();\n}\n\nfunction closePage() {\n  emit(\"close\");\n}\n</script>\n\n<template>\n  <div class=\"header\">\n    <span class=\"prev-button\" @click=\"closePage\"><i class=\"mdi mdi-arrow-left-box mdi-30px\"\n        style=\"font-size: 30px;\"></i></span>\n    <div class=\"icon-container small\">\n      <img src=\"../../../../../../src/images/is-bankasi-logo.jpg\" alt=\"is-bankasi-logo\" class=\"header-logo\" width=\"16\"\n        height=\"16\" />\n    </div>\n\n    <label class=\"app-name\">{{ viewModel.studio.appName }}</label>\n    <label class=\"app-name\" style=\"margin-left: 12px;\">/</label>\n    <label class=\"progress-details-text\">Progress Details</label>\n    <div style=\"display: flex; margin-left: 1450px;\">\n      <i class=\"mdi mdi-account-circle profile-icon\"></i>\n      <i class=\"mdi mdi-chevron-down chevronIcon\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\"\n        data-bs-target=\"#dropdown-menu\" id=\"dropdownMenuButton1\" style=\"padding-top: 16px; margin-right: -11px\"></i>\n      <ul style=\"width: 246px\" class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\