  <div class=\"table-row\" v-for=\"row in tableData\">\n            <span class='table-row-text' style=\"margin-left: 20px;width: 403px;float: left;\">{{ row.title }}</span>\n            <span class='table-row-text' style=\"width: 228px; float: left;\">{{ row.createDate }}</span>\n            <span class='table-row-text' style=\"width: 220px; float: left;\">{{ row.createdBy }}</span>\n            <span class=\"table-row-status\" style=\"float: left; width: 20px;\">\n              <i class=\"mdi mdi-timer-sand table-row-status-icon\" v-if=\"row.status == 'waiting'\"></i>\n              <i class=\"mdi mdi-arrow-up-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'running'\"></i>\n              <i class=\"mdi mdi-check-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'success'\"></i>\n              <i class=\"mdi mdi-close-circle-outline table-row-status-icon\" v-else-if=\"row.status == 'failed'\"></i>\n            </span>\n            <span class='table-row-text' style=\"float: left;width: 136px;\">{{ row.status }}</span>\n            <span class='table-row-text' style=\"float: left;width: 230px;\">{{ row.platform }}</span>\n            <span class='row-down-icon' @click=\"openCloseRow(row)\">\n              <i class=\"mdi mdi-chevron-down\" style=\"padding-top: 13px;\" v-if=\"!row.showInformation\"></i>\n              <i class=\"mdi mdi-chevron-up\" style=\"padding-top: 13px;\" v-else></i>\n            </span>\n            <div :id=\"row.title\" class=\"row-information-container\" :style=\"{ height: `${40 * row.steps.length}px` }\"\n              v-if=\"row.showInformation\">\n              <RowInformationContainer v-for=\"step in row.steps\" :ID=\"step.ID\" :name=\"step.name\"\n                :status=\"step.status\" />\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n<style scoped>\n.header {\n  display: flex;\n  align-items: center;\n  padding: 19px 17px 19px 0px;\n  height: 68p