  let historyList = await qcloudApi.listApplicationJobHistory({ applicationID: viewModel.studio.appId, type: showingTab.value });

  tableData.value = historyList.map(e => {
    return {
      title: e.ID,
      createDate: e.createDate.toString(),
      createdBy: e.createdBy,
      status: e.status,
      platform: !e.platform ? "-" : e.platform,
      steps: [],
      showInformation: false,
      isMultiStepJob: e.isMultiStep
    } as PublishHistoryTableRow
  })
